import React from "react"
import { makeStyles } from "@material-ui/core/styles"
import { Card } from "@material-ui/core"
import { CardActionArea } from "@material-ui/core"
import { CardActions } from "@material-ui/core"
import { CardContent } from "@material-ui/core"
import { CardMedia } from "@material-ui/core"
import { Typography } from "@material-ui/core"
import DeleteIcon from "@material-ui/icons/Delete"
import EditIcon from "@material-ui/icons/Edit"
import IconButton from "@material-ui/core/IconButton"
import firebase from "../firebase"


const useStyles = makeStyles((theme) => ({
	cardContainer: {
		background: "#c0c0c0",
		width: "100%"
	},
	cardContent: {
		lineHeight: 1.25
	}
}))

function LinkCard(props) {

	const classes = useStyles()
	let hostName = getHostName(props.url)

	return (
		<>
			<Card className={classes.cardContainer} >
				<CardActionArea>
					{props.img !== "" 
						? 
							<CardMedia
								component="img"
								alt="Image"
								image={props.img}
								title="cat"
							/> 
						: ""}
					<CardContent>
						<Typography variant="overline">{hostName}</Typography>
						<Typography className={classes.cardContent} variant="subtitle1" gutterBottom>{props.title}</Typography>
						<Typography className={classes.cardContent} variant="caption" gutterBottom>{props.url}</Typography>
					</CardContent>
				</CardActionArea>
				<CardActions>
					<IconButton size="small" color="primary">
						<EditIcon />
					</IconButton>
					<IconButton size="small" color="primary"
						onClick={(e) => deleteLinkCard(props.contentId)}>
						<DeleteIcon />
					</IconButton>
				</CardActions>
			</Card>
		</>
	)
}

function getHostName(url) {
	const matches = url.match(/^https?:\/\/(www.)?([^/?#]+)(?:[/?#]|$)/i)
	return matches && matches[2]
}

function deleteLinkCard(id) {

	firebase.firestore()
		.collection("links")
		.doc(id)
		.delete()
		.then(() =>
			console.log("Success")
		).catch((err) =>
			console.error("Error removing document: ", err)
		)
}

export default LinkCard