import * as React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid';
import FolderIcon from '@material-ui/icons/Folder';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from "@material-ui/icons/Edit"

import StackGrid from "react-stack-grid";
import firebase from "../firebase"
import { useState, useEffect } from "react"
import Favorites from './Favorites';
import Home from './Home';
import Folder from './Folder';

import { BrowserRouter as Router, 
  Link, Route, Switch, useRouteMatch, useParams } from 'react-router-dom';
import Header from '../components/Header';
import FormDialog from '../components/FormDialog';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
		width: `calc(100vw - ${theme.spacing(2)}px)`,
		marginLeft: theme.spacing(1),
		marginRight: theme.spacing(1),
		marginTop: theme.spacing(9),
		marginBottom: theme.spacing(16)
  },
  demo: {
    backgroundColor: theme.palette.background.paper,
  },
  title: {
    margin: theme.spacing(4, 0, 2),
  },
  folderDefault: {
    position: "relative",
    top: "100%",
    background: "red",
    transition: "transform 0.3s ease-in"
  },
  folderHidden: {
    transform: "translate3d(-100%,0,0)"
  },
  folderShown: {
    transform: "translate3d(0,0,0)"
  },
  link: {
    textDecoration: "none"
  }
}));

export default function Collection() {

  const [folderId, setFolderId] = useState("")
  const classes = useStyles()
  const folderData = useDetails()
  let { path, url } = useRouteMatch();

  const collections = folderData.map(folder =>
    
      <Grid item >
        <Link className={classes.link} to="/folder" key={folder.folder_id}>
        <ListItem button onClick={e => setFolderId(folder.folder_id)}>
          <ListItemAvatar>
            <Avatar>
              <FolderIcon />
            </Avatar>
          </ListItemAvatar>
          <ListItemText
            primary={folder.folder_name}
            secondary={folder.folder_desc}
          />
          <ListItemSecondaryAction>
            <IconButton edge="end" aria-label="edit">
              <EditIcon />
            </IconButton>
            <IconButton edge="end" aria-label="delete"
              onClick={e => deleteCollection(folder.folder_id)}>
              <DeleteIcon />
            </IconButton>
          </ListItemSecondaryAction>
        </ListItem>
        </Link>
      </Grid>
    
  )

  return (
    <>
      <Router>
      <Header title={"Collection"} />
      <div className={classes.root}>
        <Switch>
          <Route exact path="/collection" render={() => {
            return (
            <List className={classes.demo}>
              <StackGrid columnWidth="100%">
                {collections}
              </StackGrid>
            </List>
          )}} />
          <Route path="/folder">
            <Folder id={folderId}/>
          </Route>
        </Switch>
      </div>
      </Router>
      <FormDialog mode={"collection"} />
    </>
  );
}

function deleteCollection(id) {

	firebase.firestore()
		.collection("collections")
		.doc(id)
		.delete()
		.then(() =>
			console.log("Success")
		).catch((err) =>
			console.error("Error removing document: ", err)
		)
}

function useDetails() {
	const [folderDetails, setFolderDetails] = useState([])

	useEffect(() => {
		const unsubscribe = firebase
			.firestore()
			.collection("collections")
			.onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					folder_id: doc.id,
					...doc.data()
				}))
				setFolderDetails(newDetails)
			})
        
        return () => unsubscribe()
    }, [])
    
    return folderDetails
}