import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import { makeStyles } from '@material-ui/core';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import MenuItem from '@material-ui/core/MenuItem';
import { useState, useEffect } from "react"

import firebase from "../firebase"
import axios from "axios"
import cheerio from "cheerio"

const useStyles = makeStyles(theme => ({
  fabAdd: {
		position: "fixed",
		bottom: theme.spacing(8),
		right: theme.spacing(2),
		zIndex: 1000
  },
  formHolder: {
    position: "fixed",
    width: "100%",
    bottom: 0,
    padding: `${theme.spacing(3)}px ${theme.spacing(2)}px`,
    borderRadius: `${theme.spacing(1)}px ${theme.spacing(1)}px 0 0`,
    background: "#999",
    zIndex: 5,
    transition: "transform 0.3s ease-in-out"
  },
  formShown: {
      transform: "translate3d(0,0,0)"
  },
  formHidden: {
      transform: "translate3d(0,100%,0)"
  },
  formInput: {
      width: "100%",
      margin: `${theme.spacing(1)}px 0`
  },
  formTitle: {
      display: "flex",
      alignItems: "center",
      textTransform: "uppercase",
      paddingBottom: theme.spacing(1)
  },
  buttonHolder: {
      margin: `${theme.spacing(1)}px 0`
  },
  formButton: {
      marginRight: `${theme.spacing(1)}px`
  }
}))

export default function FormDialog(props) {

  const classes = useStyles();
  const folders = useDetails()
  const [open, setOpen] = useState(false);

  console.log(folders)

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const [linkInFolder, setLinkInFolder] = useState("")
  const [linkTitle, setLinkTitle] = useState("")
  const [linkUrl, setLinkUrl] = useState("")

  const handleFolderSelect = e => {
    setLinkInFolder(e.target.value)
  }

  const handleTitleInput = e => {
    setLinkTitle(e.target.value)
  }

  const handleUrlInput = e => {
    setLinkUrl(e.target.value)
  }

  const [collectionName, setCollectionName] = useState("")
  const [collectionDesc, setCollectionDesc] = useState("")

  const handleCollectionNameInput = e => {
    setCollectionName(e.target.value)
  }

  const handleCollectionDescInput = e => {
    setCollectionDesc(e.target.value)
  }

  const handleFormSubmit = e => {

    e.preventDefault()
    switch(props.mode) {
      case "collection": {
        firebase.firestore()
        .collection("collections")
        .add({
          folder_name: collectionName,
          folder_desc: collectionDesc
        })
        break
      }
      default: 
        axios.get("https://cors-anywhere.herokuapp.com/" + linkUrl)
          .then(
            res => {
              console.log(res.data)
              const $ = cheerio.load(res.data)

              let linkImg = $("meta[property='og:image']").attr("content")
              if(linkImg === undefined) linkImg = $("meta[property='twitter:image']").attr("content")
              if(linkImg === undefined) {
                $("img").each((i, link) => {
                  if(/^https:\/\//.test(link.attribs.src)) {
                    linkImg = link.attribs.src
                  }
                })
              }
              if(linkImg === undefined) linkImg = ""
              
              firebase.firestore()
                .collection("links")
                .add({
                  link_title: $('title')[0].children[0].data,
                  link_url: linkUrl,
                  folder_id: linkInFolder,
                  link_img: linkImg
                })
                .then(() =>
                  alert("Link successfully added")
                ).catch(err => 
                  console.error("Error adding to database: ", err)
                )
            }
          )
        
    }
    resetFields()
    setOpen(false)
    
  }

  function resetFields() {
    setLinkInFolder("0")
    setLinkTitle("")
    setLinkUrl("")
    setCollectionName("")
    setCollectionDesc("")
  }

  /* function test() {

    let data = "TANGINA";
  
      axios.get("https://cors-anywhere.herokuapp.com/https://robwu.nl/cors-anywhere.html")
        .then(
          res => {
            console.log(res.data)
            const $ = cheerio.load(res.data)
            console.log($('title')[0].children[0].data)
            data = $('title')[0].children[0].data
          }
        )
  
    return data
  } */

  return (
    <div>
      <Fab color="primary" aria-label="add"
        className={classes.fabAdd}
        onClick={handleClickOpen}
      >
        <AddIcon />
      </Fab>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <form onSubmit={handleFormSubmit} >
          <DialogTitle id="form-dialog-title">
            Add new {props.mode === "collection" ? props.mode : "link"}
          </DialogTitle>
          <DialogContent>
            {props.mode === "collection"
              ? <>
                  <TextField
                    required
                    className={classes.formInput}
                    id="collection-name"
                    label="Collection name"
                    variant="outlined"
                    value={collectionName}
                    onChange={handleCollectionNameInput}
                    size="small"
                  />
                  <TextField
                    multiline
                    className={classes.formInput}
                    id="collection-desc"
                    label="Collection description"
                    variant="outlined"
                    value={collectionDesc}
                    onChange={handleCollectionDescInput}
                    size="small"
                  />
                </>
              : <>
                  <TextField
                    multiline
                    className={classes.formInput}
                    id={"link-title"}
                    label="Link title"
                    variant="outlined"
                    value={linkTitle}
                    onChange={handleTitleInput}
                    size="small"
                    helperText="Type your own title"
                  />
                  <TextField
                    required
                    type="url"
                    className={classes.formInput}
                    id="link-url"
                    label="Link URL"
                    variant="outlined"
                    size="small"
                    value={linkUrl}
                    onChange={handleUrlInput}
                    helperText="URL must start with http:// or https://"
                  />
                  <TextField
                    className={classes.formInput}
                    id="link-in-folder-id"
                    select
                    label="Collection"
                    value={linkInFolder}
                    onChange={handleFolderSelect}
                    variant="outlined"
                    size="small"
                  >
                    {folders.map((option) => (
                        <MenuItem 
                          key={option.folder_id} 
                          value={option.folder_id}
                        >
                          {option.folder_name}
                        </MenuItem>
                    ))}
                  </TextField>
                </>
            }
          </DialogContent>
          <DialogActions>
            <Button type="submit" color="primary">
              Save
            </Button>
            <Button onClick={handleClose} color="primary">
              Cancel
            </Button>
          </DialogActions>
        </form>
      </Dialog>
    </div>
  );
}

function useDetails() {
	const [folderDetails, setFolderDetails] = useState([])

	useEffect(() => {
    //const unsubscribe = 
    firebase
			.firestore()
			.collection("collections")
			.onSnapshot((snapshot) => {
				const newDetails = snapshot.docs.map((doc) => ({
					folder_id: doc.id,
					...doc.data()
				}))
				setFolderDetails(newDetails)
			})
        
      //return () => unsubscribe()
    }, [])
    
    return folderDetails
}